.nav {
  $this: &;
  @include light;
  @include lg {
    color: $black;
    font-size: 24px;
    text-align: center;
    display: inline-block;
    vertical-align: middle; }
  li {
    display: inline-block;
    position: relative;
    @include notlast {
      margin-right: 45px; }
    @include lg {
      display: block;
      @include notlast {
        margin-right: 0;
        margin-bottom: 15px; } }
    &.has-dropdown {
      a {
        position: relative;
        padding-right: 15px;
        @include before {
          @include icon-arrow(8, 8, 1, currentColor, -45);
          position: absolute;
          top: .2em;
          right: 0;
          @include lg {
            top: .5em; } } }
      &:hover {
        #{$this}--dropdown {
          visibility: visible;
          opacity: 1; } } } }
  a {
    @include tr(color .3s);
    text-transform: uppercase;
    display: inline-block;
    @include hover {
      color: $gray; } }

  &--dropdown {
    visibility: hidden;
    opacity: 0;
    position: absolute;
    z-index: 1;
    top: calc(100%);
    padding-top: 25px;
    left: 0;
    color: $gray;
    white-space: nowrap;
    @include tr(opacity .3s, visibility .3s);
    @include lg {
      // position: static
 }      // display: block
    li {
      display: block;
      @include notlast {
        margin-bottom: 15px; } }
    a {
      text-transform: none;
      @include before {
        display: none; }
      @include hover {
        color: $white; } } } }
