strong {
  font-weight: 400;
  @include bold; }

small {
  font-size: inherit; }

em, i {
  font-style: normal;
  @include italic; }

h1, h2, h3, h4, h5, h6 {
  font-weight: 400; }

.h1, h1 {
  fz {}
  &--white {
    color: $white; }
  &--ttn {
    text-transform: none; } }

.h2, h2 {
  &--white {
    color: $white; }
  &--ttn {
    text-transform: none; } }

.h3, h3 {
  @include css-lock(21, 36, 320, 1499, font-size);
  @include light;
  text-transform: uppercase;
  line-height: 1.2;
  &--white {
    color: $white; }
  &--ttn {
    text-transform: none; } }

.h4, h4 {
  @include css-lock(18, 24, 320, 1499, font-size);
  text-transform: uppercase;
  &--white {
    color: $white; }
  &--ttn {
    text-transform: none; } }

.h5, h5 {
  &--white {
    color: $white; }
  &--ttn {
    text-transform: none; } }

.h6, h6 {
  text-transform: uppercase;
  font-size: $fz;
  @include bold;
  &--white {
    color: $white; }
  &--ttn {
    text-transform: none; }
  &--lg {
    font-size: 16px; } }

.title {
  text-transform: uppercase;
  @include bold;
  @include css-lock(28, 48, 320, 1499, font-size);
  line-height: 1.2;
  small {
    font-size: .75em;
    @include light; }

  &--white {
    color: $white; }
  &--ttn {
    text-transform: none; }
  &--medium {
    @include medium; } }

.text {
  &--white {
    color: $white; }
  &--ttn {
    text-transform: none; }
  &--light {
    @include light; } }
