.section {
  position: relative;
  &--white-color {
    color: $white; }
  &--black {
    background-color: $black; } }

.s-more {
  .word {
    left: 0;
    top: $padding-lg; }
  .container {
    position: relative;
    z-index: 1; }
  &__title {
    margin-bottom: 175px; }
  &__block {
    width: percentage(8/12);
    margin-left: percentage(4/12);
    position: relative; }
  &__block-title {
    margin-bottom: 30px;
    @include lg {
      margin-bottom: 20px; }
    @include md {
      margin-bottom: 15px; }
    @include sm {
      margin-bottom: 10px; } }
  &__text {
    margin-bottom: 50px;
    @include lg {
      margin-bottom: 30px; }
    @include md {
      margin-bottom: 20px; }
    @include sm {
      margin-bottom: 15px; } }
  &__title-lg {
    margin-bottom: 90px;
    @include xl {
      margin-bottom: 70px; }
    @include lg {
      margin-bottom: 50px; }
    @include md {
      margin-bottom: 30px; }
    @include sm {
      margin-bottom: 20px; } }
  &__btn {
    text-align: right; }
  &__arrow {
    position: absolute;
    bottom: 0;
    left: 0% - percentage(360/930);
    color: $c-border;
    font-size: $fz-arrow;
    @include rotate(90); } }

.s-portfolio {
  .word {
    right: -5%; }
  &__link {
    text-transform: uppercase;
    display: inline-block;
    text-align: center;
    @include bold;
    .icon {
      display: block;
      margin-left: auto;
      margin-right: auto;
      font-size: 30px;
      margin-bottom: 5px; } }
  &__link-wrap {
    text-align: center;
    padding-top: 50px;
    @include lg {
      padding-top: 30px; }
    @include md {
      padding-top: 20px; }
    @include sm {
      padding-top: 15px; } } }

.s-services {
  .word {
    right: -7%; } }

.s-we-know {
  overflow: hidden;
  .word {
    right: -13%; }
  &__title-block {
    margin-bottom: 240px; }
  &__title {
    margin-bottom: 80px; }
  &__content {
    max-width: 930px;
    margin-bottom: 95px; } }

.s-testimonials {
  position: relative;
  @include before {
    position: absolute;
    width: 100%;
    height: 50%;
    left: 0;
    top: 0;
    z-index: 0;
    background-image: linear-gradient( 0deg, rgb(46,5,56) 0%, rgb(2,2,2) 100%); }
  &__title {
    position: relative;
    z-index: 1;
    margin-bottom: 95px;
    @include xl {
      margin-bottom: 75px; }
    @include lg {
      margin-bottom: 50px; }
    @include md {
      margin-bottom: 30px; }
    @include sm {
      margin-bottom: 20px; } }
  &__slider {
    position: relative;
    z-index: 1; } }

.s-order {
  &__inner {
    @include flex(flex-start, normal, row, wrap); }
  &__left {
    width: percentage(8/12); }
  &__right {
    width: percentage(4/12); }
  &__title {
    margin-bottom: 55px;
    @include xl {
      margin-bottom: 45px; }
    @include lg {
      margin-bottom: 35px; }
    @include md {
      margin-bottom: 25px; }
    @include sm {
      margin-bottom: 15px; } }
  &__contacts {
    margin-bottom: 50px;
    @include xl {
      margin-bottom: 40px; }
    @include lg {
      margin-bottom: 30px; }
    @include md {
      margin-bottom: 20px; }
    @include sm {
      margin-bottom: 10px; } }
  &__btns {
    @include flex(flex-start, normal, row, wrap); }
  &__btn {
    width: percentage(3/8);
    @include notlast {
      margin-right: 30px; } } }


.order-contacts {
  &__mail {
    text-transform: uppercase;
    font-size: 18px;
    @include tr(color .3s);
    margin-bottom: 10px;
    @include hover {
      color: $gray; } }
  &__phone {
    @extend .title;
    @include reg;
    @include tr(color .3s);
    @include hover {
      color: $gray; } }
  &__text {
    text-transform: uppercase;
    margin-top: 10px; } }
