.services {}


.service-item {
  $this: &;
  padding-top: 85px;
  padding-bottom: 85px;
  color: $white;
  background-color: $purple-dark;
  position: relative;
  overflow: hidden;
  @include tr(color .5s);
  @include notlast {
    border-bottom: 1px solid  $purple; }
  @include before {
    @include coverdiv;
    background-color: $white;
    z-index: 0;
    transform: translate(-100%, 0);
    @include tr(transform .5s); }
  @include hover {
    color: $black;
    border-bottom-color: transparent;
    @include before {
      transform: translate(0, 0); } }
  &__inner {
    position: relative;
    z-index: 1;
    @include flex(space-between, center); }
  &__title {
    margin-bottom: 30px;
    @include lg {
      margin-bottom: 20px; }
    @include md {
      margin-bottom: 15px; } }
  &__block {
    max-width: 590px; }
  &__link {
    .icon {
      font-size: $fz-arrow;
      @include tr(transform .3s); }
    @include hover {
      .icon {
        transform: translate(10px, 0); } } } }
