.portfolio-item {
  $this: &;
  position: relative;
  &:nth-child(odd) {
    color: $white;
    background-color: $black-lighten;
    .link-arrow {
      color: $gray;
      .icon {
        fill: $white; } }
    #{$this}__subttl {
      color: $gray-dark; }
    #{$this}__nmb {
      color: $gray-dark; } }
  &__right {
    position: absolute;
    right: 0;
    bottom: 0;
    top: 0;
    width: 50%;
    overflow: hidden;
    @include hover {
      #{$this}__img {
        animation-play-state: running; } } }
  &__content {
    width: 50%;
    height: 100%;
    height: 100vh;
    @include flex(center, normal, column); }
  &__img {
    @include cover;
    @include coverdiv;
    animation: scale 10s infinite alternate;
    animation-play-state: paused; }
  &__logo {
    position: absolute;
    top: 50%;
    transform: translate(0, -50%);
    z-index: 1;
    left: 0;
    width: 225px;
    height: 120px;
    @include flex(center, center); }
  &__subttl {
    margin-bottom: 40px;
    color: $gray-light;
    @include lg {
      margin-bottom: 30px; }
    @include md {
      margin-bottom: 20px; }
    @include sm {
      margin-bottom: 15px; } }
  &__title {
    margin-bottom: 25px;
    @include lg {
      margin-bottom: 20px; }
    @include md {
      margin-bottom: 15px; }
    @include sm {
      margin-bottom: 10px; } }
  &__nmb {
    position: absolute;
    font-size: 16px;
    top: 77px;
    right: calc(100% + 40px);
    // color: $gray-dark
    @include medium; }
  &__content-inner {
    position: relative; }
  &__text {
    margin-bottom: 90px;
    @include xl {
      margin-bottom: 70px; }
    @include lg {
      margin-bottom: 50px; }
    @include md {
      margin-bottom: 40px; }
    @include sm {
      margin-bottom: 30px; } } }

@keyframes scale {
  0% {
    transform: scale(1) rotate(0deg); }
  100% {
    transform: scale(1.4) rotate(5deg); } }
