.hero {
  background-color: $purple-dark;
  overflow: hidden;
  color: $white;
  position: relative;
  margin-top: -$header-height;
  @include cover;
  @include before {
    @include coverdiv;
    @include image('white-transp-pattern.png');
    background-repeat: repeat;
    background-size: auto;
    z-index: 0; }
  &__inner {
    @include flex(flex-start, center);
    position: relative;
    padding-top: $padding-lg;
    padding-bottom: $padding-lg;
    height: 100vh;
    z-index: 1; }
  &__content {
    width: 50%; }
  &__img {
    width: 50%;
    text-align: right;
    margin-right: -10%;
    margin-top: 5%;
    // img[data-loaded="true"]
 }    //   margin-left: -10%
  &__img-in {
    position: relative;
    display: inline-block;
    // font-size: 65vh
    @include css-lock(200, 600, 320, 1499, font-size);
    line-height: .9;
    img {
      position: absolute;
      top: .1em;
      left: 0;
      z-index: 0;
      max-width: 90%; }
    .icon {
      position: relative;
      z-index: 1;
      fill: $white;
      // fill: transparent
      // stroke: $white
      // stroke-width: 5px
      // stroke-dasharray: 15000
      // stroke-dashoffset: 15000
 } }      // animation: logo 3s forwards
  &__title {
    margin-bottom: 60px;
    .title {
      small {
        display: block; } }
    @include xl {
      margin-bottom: 50px; }
    @include lg {
      margin-bottom: 40px; }
    @include md {
      margin-bottom: 30px; }
    @include sm {
      margin-bottom: 15px; } }
  &__subttl {
    text-transform: uppercase;
    @include light;
    margin-bottom: 20px;
    @include md {
      margin-bottom: 10px; } }
  &__btns {
    @include flex; }
  &__btn {
    @include notlast {
      margin-right: 30px; } }
  &__scroll-down {
    position: absolute;
    left: 50%;
    transform: translate(-50%, 0);
    bottom: 0;
    z-index: 1; }

  @keyframes logo {
    100% {
      stroke-dashoffset: 0; } } }
