.header {
  $this: &;
  color: $white;
  position: relative;
  z-index: 15;
  height: $header-height;
  @include flex(flex-start, center);
  padding-left: 30px;
  padding-right: 30px;
  padding-top: 20px;
  &__logo {
    padding-right: 25px; }
  &__phone {
    padding-left: 25px;
    padding-right: 25px;
    position: relative;
    &::before,
    &::after {
      content: '';
      position: absolute;
      background-color: $gray;
      width: 1px;
      height: 38px;
      top: 50%;
      transform: translate(0, -50%); }
    &::before {
      left: 0; }
    &::after {
      right: 0; } }
  &__nav {
    padding-left: 25px;
    flex-grow: 1;
    @include lg {
      position: fixed;
      z-index: 15;
      width: 100%;
      height: 100%;
      top: 0;
      left: 0;
      padding: 15px;
      background-color: $white;
      @include vertical; } }
  &__burger {
    @include lg {
      flex-grow: 1; } } }

.header-phone {
  $this: &;
  @include light;
  &__inner {
    position: relative;
    &:hover {
      #{$this}__list {
        visibility: visible;
        opacity: 1; } } }

  &__phone {
    display: block; }
  &__list {
    position: absolute;
    top: 100%;
    padding-top: 25px;
    left: 0;
    z-index: 1;
    visibility: hidden;
    opacity: 0;
    @include tr(visibility .3s, opacity .3s); }
  &__subttl {
    color: $gray;
    font-size: 10px; }
  &__title {
    @include tr(color .3s);
    position: relative;
    display: inline-block;
    padding-right: 15px;
    font-size: 19px;
    @include bold;
    @include hover {
      color: $gray; }
    @include before {
      @include icon-arrow(8, 8, 1, currentColor, -45);
      position: absolute;
      top: .4em;
      right: 0; } }
  &__phone {
    @include tr(color .3s);
    @include hover {
      color: $gray; } } }

.burger {
  display: block;
  width: 39px;
  height: 18px;
  margin-left: auto;
  span:after, span:before {
    content: "";
    position: absolute;
    left: 0;
    top: 9px; }
  span:after {
    top: 18px; }
  span {
    position: relative;
    display: block; }
  span, span:after, span:before {
    width: 100%;
    height: 2px;
    background-color: currentColor;
    transition: all 0.3s;
    backface-visibility: hidden;
    border-radius: 2px; }
  &.is-active span {
    background-color: transparent; }
  &.is-active span:before {
    transform: rotate(45deg) translate(-1px, 0px); }
  &.is-active span:after {
    transform: rotate(-45deg) translate(6px, -7px); } }
