@include font('GothamPro', 'GothamPro');
@include font('GothamProBold', 'GothamProBold');
@include font('GothamProItalic', 'GothamProItalic');
@include font('GothamProLight', 'GothamProLight');
@include font('GothamProMedium', 'GothamProMedium');
@include font('GothamProBlack', 'GothamProBlack');

@mixin reg {
  font-family: 'GothamPro', Arial, sans-serif; }
@mixin bold {
  font-family: 'GothamProBold', Arial, sans-serif; }
@mixin italic {
  font-family: 'GothamProItalic', Arial, sans-serif; }
@mixin light {
  font-family: 'GothamProLight', Arial, sans-serif; }
@mixin medium {
  font-family: 'GothamProMedium', Arial, sans-serif; }
@mixin black {
  font-family: 'GothamProBlack', Arial, sans-serif; }

